The below snippet of CSS must be included into your project. Future release may bundle these additional styles. */
.mdi::before {
    font-size: 24px;
    line-height: 14px;
}
.btn .mdi::before {
    position: relative;
    top: 4px;
}
.btn-xs .mdi::before {
    font-size: 18px;
    top: 3px;
}
.btn-sm .mdi::before {
    font-size: 18px;
    top: 3px;
}
.dropdown-menu .mdi {
    width: 18px;
}
.dropdown-menu .mdi::before {
    position: relative;
    top: 4px;
    left: -8px;
}
.nav .mdi::before {
    position: relative;
    top: 4px;
}
.navbar .navbar-toggle .mdi::before {
    position: relative;
    top: 4px;
    color: #FFF;
}
.breadcrumb .mdi::before {
    position: relative;
    top: 4px;
}
.breadcrumb a:hover {
    text-decoration: none;
}
.breadcrumb a:hover span {
    text-decoration: underline;
}
.alert .mdi::before {
    position: relative;
    top: 4px;
    margin-right: 2px;
}
.input-group-addon .mdi::before {
    position: relative;
    top: 3px;
}
.navbar-brand .mdi::before {
    position: relative;
    top: 2px;
    margin-right: 2px;
}
.list-group-item .mdi::before {
    position: relative;
    top: 3px;
    left: -3px
}

/* Custom CSS */
.navbar input[type="text"].form-control {
    -webkit-transition: all 0.25s ease;
    -o-transition: all 0.25s ease;
    transition: all 0.25s ease;
    min-width: 200px;
}
.navbar input[type="text"].form-control[value]:not([value=""]),
.navbar input[type="text"].form-control:focus {
    min-width: 500px;
}
.py-5.bg-light {
    min-height: calc(100vh - 56px);
}

.items-section {
    position: relative;
}

.items-section + .items-section {
    margin-top: 30px;
}

.items-section-title a {
    font-size: .7em;
}

.items-section-body {
    position: relative;
    min-height: 271px;
}

.grid-item {
    display: block;
    position: relative;
    margin-bottom: 30px;
}

.grid-item-body {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /* visibility: hidden; */
    opacity: 0;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(0, 0, 0, .5);
    text-align: center;
}

.grid-item:hover {
    cursor: pointer;
}

.grid-item-body:hover,
.grid-item:hover .grid-item-body {
    /* visibility: visible; */
    opacity: 1;
}

.grid-item-actions > a {
    margin: 0 2px;
}

.grid-item-title {
    color: #ffffff;
    font-size: 18px;
    font-weight: bold;
}

.grid-item-date {
    color: #ffffff;
}

.detail-section {
    margin-bottom: 40px;
}

.filters-bar {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
}

.filters-bar-left {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    max-width: 60%;
}

.filters-bar-left .form-control {
    margin-right: 15px;
    -ms-flex-preferred-size: 33%;
        flex-basis: 33%;
}

.filters-bar-left .btn {
    margin-right: 15px;
}

.filters-bar-right {
    margin-left: auto;
}

.list-item {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 30px;
}
.list-item-img {
    max-width: 180px;
    min-width: 90px;
}
.list-item-body {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: #eee;
    padding: 15px;
}
.list-item-title {}
.list-item-description {
}
.list-item-actions {}

.grid-item-actions .btn + .btn,
.list-item-actions .btn + .btn {
    margin-left: 5px;
}

.search-item h3{
    font-size: 1.25rem;
}
.search-item .list-item-img {
    max-width: 90px;
}

.dimmer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(255, 255, 255, .5);
    z-index: 10;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
@-webkit-keyframes spin {
    from { -webkit-transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

.loading {
    animation: spin 1s infinite linear;
    -moz-animation: spin 1s infinite linear;
    -webkit-animation: spin 1s infinite linear;
}



